@charset 'utf-8';

@import 'variables';
@import 'typography';


* {
    margin: 0;
    padding: 0;
}

body {
    background-color: $body-bg-color;
    height: 100vh;
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
}

a {
    transition: .2s ease;
    color: $nav-link-color;

    &:hover {
        color: $nav-link-hover-color;
    }
}

a, a:hover, a:focus {
    text-decoration: none;
}

header nav, main {
    position: relative;
    padding: 2rem 10rem;
    max-width: 1380px;
    margin: 0 auto;
}

header {
    position: relative;
    padding-top: 6rem;
    background: linear-gradient(180deg, $bg-color-light 0%, $bg-color-dark 100%);
    overflow: hidden;
    min-height: 160px;

    > .ears {
        position: absolute;
        left: 50%;
        top: 2rem;
        transform: translate(-50%, 0);
    }

    nav {
        position: relative;
        top: -4rem;
        display: flex;
        align-items: center;
        flex-flow: row nowrap;
        -ms-flex-pack: start;
        justify-content: flex-start;
        text-transform: uppercase;

        h1 {
            display: inline-block;
            margin-right: 1rem;
            font-size: 2rem;
            line-height: inherit;

            a {
                display: block;
                width: 300px;
                height: 49px;
                text-indent: 100%;
                overflow: hidden;
                background-image: url('/assets/images/bitbear.png');
                background-repeat: no-repeat;
                background-size: 300px 49px;
                transition: opacity .2s ease;
            }
        }

        ul {
            font-size: 1.5rem;
            margin-left: auto;
            display: flex;

            li {
                box-sizing: border-box;
                list-style: none;
                text-align: right;
                padding: 1rem 0 1rem 1rem;
                display: block;

                a {
                    font-weight: 900;
                }
            }
        }
    }
}

header nav h1 a:hover,
footer a:hover {
    opacity: 0.8;
}

main {
    padding: 2rem 10rem;
    max-width: 70rem;
    flex: 1;
    position: relative;
    top: -6rem;
    padding: 2rem 10rem;
    margin: 0 auto;

    h1, h2, h3, h4, h5, h6 {
        font-weight: 500;
        margin: 2rem 0 .5rem 0;
        color: $nav-link-color;
        text-transform: uppercase;
        font-weight: bold;
    }

    p {
        margin: .5rem 0 2rem 0;
    }

    ul {
        margin-left: 1em;
    }
}

.jumbotron {
    background-color: rgba($bg-color-light, .4);
    border-radius: 1rem;
    padding: 5rem;
    margin: 3rem 0;
    font-size: 3rem;
}

footer {
    position: relative;
    min-height: 100px;
    max-height: 300px;
    background: linear-gradient(180deg, $bg-color-dark 0%, $bg-color-light 100%);
    padding-top: 2rem;

    a {
        font-size: 1.2rem;
        text-align: center;
        display: block;
        width: 14rem;
        margin: 2rem auto 0 auto;
        color: $text-color;
        opacity: .3;

        span {
            display: block;
            padding: 1rem 0 2rem 0;
        }
    }
}


@media screen and (max-width: 745px) {
    body {
        font-size: 1rem;
    }

    header {
        padding: 1rem 2rem;
        min-height: 80px;
        box-sizing: border-box;

        nav {
            top: 0;
            padding: 0;

            h1 a {
                width: 60px;
                background-image: url('/assets/images/bitbear-head.png');
                background-size: 60px 49px;
            }
        }
    }

    main {
        padding: 0 2rem;
        top: -2rem;
    }

    .jumbotron {
        font-size: 1.5rem;
        padding: 0;
        background-color: inherit;
    }

    footer {
        padding: 0;
    }
}
